<template>
<div class="main-content">
  <filter-toolbar :class="{'hidden-xs-only': !isFilterToolbarDisplayed}"/>
  <v-layout class="full-height" row wrap>
    <v-flex md7 sm12 :class="{'hidden-sm-and-down': !isListDisplayed, 'hidden-drawer': !isListDisplayed}" style="position: relative">
      <house-list :announcments="announcments"/>
      <list-switch :isListDisplayed="isListDisplayed" :switchView="switchView"/>
    </v-flex>
    <v-flex md5 sm12 :class="{'hidden-sm-and-down': isListDisplayed, 'map-expanded': !isListDisplayed}">
      <house-map/>
    </v-flex>
  </v-layout>
    <bottom-toolbox class="hidden-sm-and-up" :isListDisplayed="isListDisplayed" :switchView="switchView" :switchFilters="switchFilters" />
</div>
</template>

<script>
  import store from "@/store";
  import { mapState } from "vuex";

  import HouseList from "./scenes/List/List.vue";
  import HouseMap from "./scenes/Map/Map.vue";
  import FilterToolbar from "./scenes/FilterToolbar/FilterToolbar.vue";
  import ListSwitch from "./components/ListSwitch.desktop.vue";
  import BottomToolbox from "./components/BottomToolbox.mobile.vue";
  import { FETCH_ANNOUNCMENTS } from "./vuex/actions";

  export default {
    name: "landing",
    data: function() {
      return {
        isListDisplayed: true,
        isFilterToolbarDisplayed: false
      };
    },
    computed: mapState({
      announcments: state => state.announcments.announcments
    }),
    created: function() {
      store.dispatch(FETCH_ANNOUNCMENTS);
    },
    methods: {
      switchView: function() {
        this.isListDisplayed = !this.isListDisplayed;
        this.isFilterToolbarDisplayed = false;
      },
      switchFilters: function() {
        this.isFilterToolbarDisplayed = !this.isFilterToolbarDisplayed;
      }
    },
    components: {
      HouseList,
      HouseMap,
      FilterToolbar,
      ListSwitch,
      BottomToolbox
    }
  };
</script>

<style lang="sass" scoped>
  .main-content
    height: 94%
    @media screen and (max-width: 1000px)
      height: 94%
    @media screen and (max-width: 600px)
      height: 90%
      // display: flex
  .full-height
    height: 88%
    @media screen and (max-width: 1000px)
      height: 80%
    @media screen and (max-width: 600px)
      height: 88%
  .hidden-drawer
    max-width: 0%
  .map-expanded
    min-width: 100%
</style>
