<template>
    <v-card dark color="secondary">
        <gmap-map
            :center="{lat: 52.237049, lng: 21.017532}"
            :zoom="12"
            :options="mapOptions"
            @bounds_changed="updateRectangle($event)"
            style="width: 100%; height: 100%"
            >
        </gmap-map>
    </v-card>
</template>

<script>
import ExactMapMarker from "./components/ExactMapMarker.vue";
import mapStyles from "./mapStyles.js";
import _ from "lodash";
const logBoundingBox = _.debounce(event => console.log(event.j, event.l), 500);

export default {
  data: function() {
    return {
      mapOptions: {
        mapTypeControl: false,
        styles: mapStyles
      },
      myicon:
        "https://www.shareicon.net/data/48x48/2015/10/04/112004_home_512x512.png"
    };
  },
  methods: {
    updateRectangle(event) {
      if (event) {
        logBoundingBox(event);
      }
    }
  },
  components: {
    ExactMapMarker
  }
};
</script>

<style lang="sass" scoped>
    .v-card
      height: 100%
    .vue-map-container
      position: static !important
</style>
