import Vue from "vue";
import Router from "vue-router";

import Landing from "@/scenes/Main/Landing.vue";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "landing",
      component: Landing
    },
    {
      path: "/",
      name: "rooms",
      component: () => import("@/scenes/Rooms/Rooms.vue")
    },
    {
      path: "/details/:id",
      name: "details",
      component: () => import("@/scenes/Details/Details.vue")
    },
    {
      path: "/signin",
      name: "signin",
      component: () => import("@/scenes/Authentication/SignIn/SignIn.vue")
    },
    {
      path: "/signup",
      name: "signup",
      component: () => import("@/scenes/Authentication/SignUp/SignUp.vue")
    },
    {
      path: "/profile",
      name: "profile",
      component: () => import("@/scenes/Profile/Profile.vue")
    },
    {
      path: "/addnew",
      name: "addnew",
      component: () => import("@/scenes/AddNew/AddNew.vue")
    },
    {
      path: "/roommates",
      name: "roommates",
      component: () => import("@/scenes/Roommates/Roommates.vue")
    }
  ]
});
