<template>
   <v-bottom-nav
      :active.sync="bottomNav"
      :value="true"
      color="#1e1e1e"
    >
      <v-btn
        color="#fff"
        value="filter"
        flat
        @click="switchFilters"
      >
        <span>Filter</span>
        <v-icon >filter_list</v-icon>
      </v-btn>

      <v-btn
        color="#fb7600"
        flat
        value="switch"
        @click="switchView"
      >
        <span>{{isListDisplayed ? "Go To Map" : "Back To List"}}</span>
        <v-icon >{{isListDisplayed ? "map" : "arrow_back"}}</v-icon>
      </v-btn>

      <v-btn
        color="#fff"
        value="sort"
        flat
      >
        <span>Sort</span>
        <v-icon >sort</v-icon>
      </v-btn>
    </v-bottom-nav>
</template>

<script>
export default {
  data() {
    return {
      bottomNav: "switch"
    };
  },
  props: {
    isListDisplayed: Boolean,
    switchView: Function,
    switchFilters: Function
  }
};
</script>

<style lang="sass" scoped>
    .v-bottom-nav
      height: 12% !important
</style>
