<template>
<div class="ma-3 cont">
    <router-link to="/details/some-id">
        <div
        class="ann"
        >
        <div style="height: 220px; width: 100%;">
        <v-img v-if="dataLoaded"
          :src="data.imgUrl"
          width="100%"
          class="ann-img"
        ></v-img>
        <img v-else
          :src="randomPlaceholder()"
          width="100%"
          class="ann-img"/>
        </div>
          <div class="pa-3 data">
            <div v-if="dataLoaded" class="price">
              <span>{{data.price}} zł</span>
              <span>{{data.rooms}} {{data.rooms > 1 ? "rooms" : "room"}}</span>
            </div>
            <div v-if="dataLoaded" class="address">
              <span>{{data.address}}</span>
              <span>{{data.area}} m2</span>
            </div>
          </div>
        </div>
    </router-link>
        <heart v-if="dataLoaded" :favorite="data.isFavorite" :id="data.id" class="favorite"/>
</div>
</template>

<script>
  import Heart from "./Heart.vue";
  import placeholder1 from "@/assets/placeholder.png";
  import placeholder2 from "@/assets/placeholder2.png";

  export default {
    components: {
      Heart
    },
    computed: {
      dataLoaded() {
        return Boolean(this.data.imgUrl);
      }
    },
    props: {
      data: Object
    },
    methods: {
      randomPlaceholder() {
        const placeholders = [placeholder1, placeholder2];
        const number = Math.floor(Math.random() * placeholders.length);
        return placeholders[number];
      }
    }
  };
</script>

<style lang="sass" scoped>
    a
      text-decoration: none
    .cont
      position: relative
    .ann
      cursor: pointer
      height: 300px
      width: 300px
    .ann-img
      height: 220px
      border-top-right-radius: 20px
    .data
      height: 80px
      display: flex
      flex-direction: column
      justify-content: space-between
      background: #fff
      border-bottom-left-radius: 20px
      border-bottom-right-radius: 20px
    .price
      color: #fb7600
      font-size: 20px
      display: flex
      justify-content: space-between
      font-weight: bold
    .address
      color: #1e1e1e
      font-weight: bold
      font-size: 14px
      display: flex
      justify-content: space-between
    .favorite
      position: absolute
      top: 0
      left: 0
</style>
