import axios from "axios";

let emptyAnnouncments = Array(18).fill(0)
let images = [
    'https://i.ebayimg.com/00/s/MzYwWDY1MA==/z/ZzAAAOSwpvZZ8HPN/$_86.JPG',
    'http://4.bp.blogspot.com/-RSAdi3NMMs8/VakWj_znRcI/AAAAAAAAAMI/lp19iktRyCw/s1600/Rent%2Broom%2Bstockholm.jpg',
    'https://d1bvpoagx8hqbg.cloudfront.net/originals/doubles-rooms-rent-preston-ab4ba2162b3d15c252d63657c3136214.jpg',
    'https://img01-olxpl.akamaized.net/img-olxpl/717899927_1_644x461_1-osobowy-pokoj-al-pokoju-grzegorzki-dabie-single-room-to-rent-en-krakow.jpg',
    'https://d1bvpoagx8hqbg.cloudfront.net/originals/single-room-rent-new-house-e60abc920028735be1070f80f99844f6.jpg'
]

export const fetchAnnouncmentsRequest = async payload =>
    sleep(() => {
        return {
            data: {
                announcments: emptyAnnouncments.map((val, ind) =>
                    ({
                        id: `${ind}`,
                        imgUrl: images[Math.floor(Math.random() * 5)],
                        price: Math.floor(Math.random() * 2000 + 1300),
                        rooms: Math.floor(Math.random() * 4 + 1),
                        address: `Grójecka ${Math.floor(Math.random() * 180 + 1)}`,
                        area: Math.floor(Math.random() * 50 + 40),
                        location: {
                            lat: 51.737049 + Math.random(),
                            lng: 20.517532 + Math.random()
                        },
                        isFavorite: Math.random() > 0.5 ? true : false
                    })
                ),
                success: true
            }
        };
    }, payload);

/// payload {id}
export const postFavoriteRequest = async payload =>
    sleep(() => {
        return {
            data: {
                success: true,
            }
        };
    }, payload);

function sleep(fn, par) {
    return new Promise(resolve => {
        setTimeout(() => resolve(fn(par)), 2000);
    });
}